<template>
  <div class="index mainpage">
    <div class="infos">
      <div class="infos-block">
        <i class="el-icon-edit icon green"></i>今日发标数
        <div class="content">300</div>
      </div>
      <div class="infos-block">
        <i class="el-icon-info icon bule"></i>今日发标数
        <div class="content">600</div>
      </div>
      <div class="infos-block">
        <i class="el-icon-success icon red"></i>今日发标数
        <div class="content">900</div>
      </div>
      <div class="infos-block">
        <i class="el-icon-date icon yellow"></i>今日发标数
        <div class="content">1300</div>
      </div>
    </div>

    <div class="todo-panel">
      <div class="panel">
        <div class="panel-heading">待处理事项</div>
        <div class="panel-body" style="padding: 15px;">
          <template v-for="(item, index) in items">
            <div class="todo-info" :key="index">
              <span class="todo-title">{{item.title}}</span>  
              <span class="todo-link">（{{item.num}}）</span>  
            </div>
          </template>
        </div>
      </div>
    </div>

    <div class="entry-panel">
      <div class="panel">
        <div class="panel-heading">快捷入口</div>
        <div class="panel-body" style="padding: 15px;">
          <div class="entry">
            <i class="el-icon-circle-plus icon red"></i>
            <div class="text">添加菜单</div>
          </div>
          <div class="entry">
            <i class="el-icon-document icon bule"></i>
            <div class="text">报表统计</div>
          </div>
          <div class="entry">
            <i class="el-icon-search icon yellow"></i>
            <div class="text">查询标的</div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    name: 'index',
    data () {
      let arr = []
      for (let i = 1; i <= 15; i++) {
        arr.push({
          title: '待审核的标',
          num: `${i}`
        })
      }
      return {
        items: arr
      }
    }
  }
</script>
<style lang="scss" type="text/css">
  .mainpage {
    color: #747474;
    .green {
      color: #3D9400;
    }
    .bule {
      color: #569099;
    }
    .red {
      color: #C83A2A;
    }
    .yellow {
      color: #f0ad4e;
    }
    .panel-heading {
      line-height: 40px;
    }
  }
  .infos {
    margin-top: 15px;
    margin-bottom: 25px;
    .icon{
      font-size: 32px;
      margin-right: 15px;
    }
    .infos-block {
      display: inline-block;
      width: 20%;
      border-bottom: 1px solid #EEE;
      margin-right: 6%;
      padding-bottom: 15px;
    }
    .infos-block:last-child {
      margin-right: 0;
    }
    .content {
      text-align: center;
      margin-top: 15px;
    }
  }
  .todo-panel {
    margin-bottom: 25px;
    .todo-info {
      cursor: pointer;
      display: inline-block;
      width: 28%;
      margin-right: 5%;
      line-height: 30px;
      border-bottom: 1px solid #EEE;
      .todo-link {
        float: right;
      }
    }
  }
  .entry-panel {
    .entry {
      cursor: pointer;
      display: inline-block;
      text-align: center;
      margin-right: 15px;
    }
    .text {
      margin-top: 15px;
    }
    .icon {
      font-size: 32px;
    }
  }
</style>


